import { graphql } from 'gatsby';
import React from 'react';

import Framework from '../components/framework';
import SEO from '../components/framework/seo';

export const query = graphql`
	query Products {
		allCustomApi {
			edges {
				node {
					products {
						id
						image_url
						name
						price
						product_name
						url
					}
				}
			}
		}

		background: file(relativePath: { eq: "shop/background.jpg" }) {
			childImageSharp {
				fluid(maxWidth: 1140) {
					...GatsbyImageSharpFluid
				}
			}
		}
	}
`;

const ShopPage = ({ data, location, pageContext }) => {
	const exists = {};
	const filterFunctions = value => {
		if (!exists[value.name]) {
			exists[value.name] = true;
			return true;
		}
		return false;
	};
	const header = {
		image: data.background.childImageSharp.fluid.src,
		subTitle: 'Denizko Art',
		title: 'Shop'
	};
	const products = data.allCustomApi.edges[0].node.products;

	return (
		<Framework header={header} location={location}>
			<SEO title="Shop" />
			<div className="container">
				<div className="row row-cols-1 row-cols-md-3">
					{products.filter(filterFunctions).map((product, index) => {
						return (
							<div className="col mb-4" key={index}>
								<div
									className="bg-white card mb-5 p-3 rounded shadow-sm text-center"
									key={product.id}
								>
									<a
										href={`https://teespring.com${product.url}`}
										rel="noopener noreferrer"
										target="_blank"
									>
										<img
											alt={product.name}
											className="img-fluid"
											src={product.image_url}
										/>
										<span className="small">{product.name}</span>
									</a>
								</div>
							</div>
						);
					})}
				</div>

				<div className="text-center">
					<a
						className="btn btn-outline-secondary"
						href="https://shop.denizko.me"
						rel="noopener noreferrer"
						target="_blank"
					>
						More products
					</a>
				</div>
			</div>
		</Framework>
	);
};

export default ShopPage;
